import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  TouchableOpacity,
  FlatList,
} from 'react-native';
import {Icon} from 'react-native-elements'




var globaltiles = [
  {
    title: "O nás",
    action: (history) => {history.push("/appinfo/about")}
  },
  {
    title: "FAQ",
    action: (history) => {history.push("/appinfo/other")}
  },
  {
    title: "Legal",
    action: (history) => {history.push("/appinfo/other")}
  },
  {
    title: "Pomoc",
    action: (history) => {history.push("/appinfo/other")}
  },
]

export default class MoreScreen extends Component {
  _flatListRenderer({item}){

    return (
      <TouchableOpacity style={styles.tilestyle} onPress={() => {item.action(this.props.history)}}>
          <Text style={styles.textstyle}>
            {item.title}
          </Text>
          <Icon name='ios-arrow-forward' type='ionicon' size={35} color={'#555'} />
      </TouchableOpacity>
    )
  }
  render() {
    return (
      <View style={styles.container}>
        <FlatList style={styles.flatliststyle}
        data={globaltiles}
        renderItem={this._flatListRenderer.bind(this)}
        keyExtractor={(item,index) => item.title}
        />
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container:{
    flex: 1,
    justifyContent: 'center',
    alignItems: 'stretch',
  },
  flatliststyle: {
    marginHorizontal: 10
  },
  textstyle: {
    fontWeight: 'bold',
    fontFamily: 'Lato',
    color: '#555',
    fontSize: 20,
    width: 200
    // TODO this won't work on some devices, especially with smaller screen
    // Fakt nechapem preco sa rozlozi text na viac riadkov,
    // ak pouzijem flexDirection: row a nenastavim width, nadavky nepomahaju
  },
  tilestyle: {
    marginTop: 10,
    flexDirection: 'row',
    justifyContent: 'space-between',
  },
})
