import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';

import ReactGA from 'react-ga';
import iconFont from 'react-native-vector-icons/Fonts/FontAwesome5_Regular.ttf';


// --------------GOOGLE ANALYTICS CONFIG --------------

const trackingId = "UA-156100319-2"; // Replace with your Google Analytics tracking ID
ReactGA.initialize(trackingId);



// Generate required css
// TODO NOT WORKING, instead fonts is imported in public.html
console.log(iconFont)
const iconFontStyles = `@font-face {
  src: url(${iconFont});
  font-family: 'FontAwesome5_Regular'
}`;

// Create stylesheet
const style = document.createElement('style');
style.type = 'text/css';
if (style.styleSheet) {
  style.styleSheet.cssText = iconFontStyles;
} else {
  style.appendChild(document.createTextNode(iconFontStyles));
}

// Inject stylesheet
document.head.appendChild(style);
//-----------------------------------------------------

ReactDOM.render(<App />, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
